import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import Link from './Link';

import styles from './Episodes.module.scss';

const Episodes = () => {
  const data = useStaticQuery(graphql`
    {
      allEpisode {
        seasons: group(field: seasonNr) {
          episodes: nodes {
            number: episodeNr
            title
          }
          number: fieldValue
        }
      }
    }
  `);

  return (
    <ul className={styles.root}>
      {data.allEpisode.seasons.map((season) => (
        <li key={season.number} className={styles.seasonItem}>
          <div className={styles.seasonName}>
            {`Season ${season.number}`}
          </div>

          <ol start="0" className={styles.episodes}>
            {season.episodes.map((episode) => (
              <li key={episode.number} className={styles.episodeItem}>
                <Link
                  to={`/episodes/s${season.number}/e${episode.number}`}
                  className={styles.episodeLink}
                >
                  {episode.title || `Episode ${episode.number}`}
                </Link>
              </li>
            ))}
          </ol>
        </li>
      ))}
    </ul>
  );
};

Episodes.displayName = 'MKEpisodes';

export default Episodes;
